//internal
import TopBar from "../Components/TopBar";
import BaseLayout from "../Layout/BaseLayout";
import { ServicesTitleContent, ServicesImage, ServicesContainer, ServicesStepContainer, ServicesStep, AccordionContainer, Ads } from "../Layout/ServicesLayout";

const SeoPage = () => {
  const steps = [
    {id:1, step:"مرحله اول", title:"ثبت درخواست مشاوره", subtitle:"در سایت با اطلاعات واقعی"},
    {id:2, step:"مرحله دوم", title:"مشاوره سئو", subtitle:"در شبکه های اجتماعی"},
    {id:3, step:"مرحله سوم", title:"ایجاد رودمپ", subtitle:"توسط کارشناسان ما"},
    {id:4, step:"مرحله چهارم", title:"استارت فرایند سئو", subtitle:"با توجه به رودمپ"},
    {id:5, step:"مرحله پنجم", title:"گزارش گیری ماهیانه", subtitle:"گزارش رشد سایت"},
    {id:6, step:"مرحله آخر", title:"افزایش رتبه سایت شما", subtitle:"پس از چنیدن ماه سئو شدن سایت"},
  ];
  const faqs = [
    {id:1, title:"چقدر طول می‌کشد تا نتایج سئو را ببینم؟", content:"مدت زمان مشاهده نتایج سئو می‌تواند متغیر باشد و به عوامل متعددی بستگی دارد. به طور کلی، سئو یک فرآیند زمان‌بر است و نتایج آن به مرور زمان نمایان می‌شود. برای وب‌سایت‌های جدید، ممکن است چندین ماه طول بکشد تا تاثیرات اولیه سئو مشاهده شود. در برخی موارد، بهینه‌سازی‌های ابتدایی می‌توانند در عرض چند هفته منجر به بهبود رتبه سایت شوند، اما برای رسیدن به نتایج پایدار و قوی‌تر، ممکن است به 6 تا 12 ماه زمان نیاز باشد. عوامل تاثیرگذار بر سرعت مشاهده نتایج شامل میزان رقابت در صنعت مورد نظر، کیفیت و کمیت محتوا، تعداد و کیفیت بک‌لینک‌ها و به‌روزرسانی‌های الگوریتم موتورهای جستجو است. همچنین، پیگیری و بهبود مداوم سئو به عنوان یک استراتژی بلندمدت بسیار مهم است تا نتایج به دست آمده حفظ و ارتقا یابد."},
    {id:2, title:"سئو چطور کار می‌کند؟", content:"سئو با بهبود جنبه‌های مختلف وب‌سایت شما باعث می‌شود که موتورهای جستجو مانند گوگل راحت‌تر وب‌سایت شما را پیدا کنند و آن را در نتایج جستجو نمایش دهند. این شامل استفاده از کلمات کلیدی مناسب در محتوای سایت، بهبود سرعت بارگذاری صفحات، و اطمینان از اینکه وب‌سایت شما بر روی دستگاه‌های مختلف خوب نمایش داده می‌شود، است. همچنین، داشتن لینک‌های خوب از سایت‌های دیگر به وب‌سایت شما نیز به بهبود رتبه‌بندی کمک می‌کند."},
    {id:3, title:"چطور می‌توانم کلمات کلیدی مناسب پیدا کنم؟", content:"کلمات کلیدی کلماتی هستند که مردم در موتورهای جستجو وارد می‌کنند تا چیزی را پیدا کنند. برای پیدا کردن کلمات کلیدی مناسب، باید بفهمید که مشتریان شما به دنبال چه چیزهایی هستند. می‌توانید از ابزارهای آنلاین مانند Google Keyword Planner استفاده کنید تا ببینید که چه کلماتی بیشتر جستجو می‌شوند. همچنین می‌توانید به رقبا نگاه کنید و ببینید که آنها از چه کلماتی استفاده می‌کنند. سپس، این کلمات را در محتوای وب‌سایت خود به کار ببرید."},
    {id:4, title:"آیا تصاویر و ویدئوها هم در سئو تاثیر دارند؟", content:"بله، تصاویر و ویدئوها می‌توانند تاثیر زیادی در سئو داشته باشند. استفاده از تصاویر و ویدئوهای با کیفیت و مرتبط می‌تواند تجربه کاربری را بهبود بخشد و بازدیدکنندگان را بیشتر در سایت نگه دارد. همچنین، بهینه‌سازی تصاویر با استفاده از تگ‌های ALT و توضیحات مناسب می‌تواند به موتورهای جستجو کمک کند تا بهتر درک کنند که تصاویر شما در مورد چیست. ویدئوها نیز می‌توانند باعث افزایش ترافیک شوند، به خصوص اگر در پلتفرم‌های اشتراک‌گذاری ویدئو مانند یوتیوب منتشر شوند و به وب‌سایت شما لینک داده شوند."},
  ];
  return(
    <BaseLayout>
      <TopBar
        title="سئو"
      />
      <div className="child:BaseContainer space-y-28">
        <ServicesTitleContent
        title="سئو چیست"
        content="سئو (SEO) یا بهینه‌سازی موتورهای جستجو، فرآیندی است که به منظور بهبود رتبه سایت شما در نتایج موتورهای جستجو انجام می‌شود. این فرآیند شامل بهینه‌سازی محتوای سایت، کلمات کلیدی و ساختار سایت می‌باشد. با استفاده از تکنیک‌های سئو، شما می‌توانید ترافیک ارگانیک بیشتری جذب کنید و بازدیدکنندگان هدفمندتری را به سایت خود بیاورید. هدف نهایی سئو، افزایش بازدید سایت و تبدیل این بازدیدها به مشتریان واقعی است."
        />
        <ServicesContainer>
          <ServicesTitleContent
          title="چرا باید سایتمون رو سئو کنیم"
          content="سئو (بهینه‌سازی موتورهای جستجو) برای هر وب‌سایتی ضروری است زیرا تأثیرات گسترده‌ای بر روی جذب ترافیک و رشد کسب‌وکار دارد. با سئو، وب‌سایت شما در نتایج جستجوی موتورهایی مانند گوگل رتبه بالاتری کسب می‌کند، که این امر باعث افزایش ترافیک ارگانیک و غیرپرداختی به سایت می‌شود. علاوه بر این، بهبود سئو منجر به تجربه کاربری بهتر می‌شود؛ سایت‌های بهینه‌شده سریع‌تر بارگذاری می‌شوند، رابط کاربری ساده‌تری دارند و محتوای مرتبط‌تری به کاربران ارائه می‌دهند. این بهبودها موجب افزایش اعتماد کاربران و اعتبار برند شما می‌شود. همچنین، در بازارهای رقابتی، سئو به شما مزیت رقابتی می‌دهد زیرا سایت‌های بهینه‌شده بیشتر دیده می‌شوند و فرصت‌های بیشتری برای جذب مشتریان جدید دارند. استفاده از ابزارهای تحلیلی در سئو به شما کمک می‌کند رفتار کاربران را درک کرده و استراتژی‌های خود را بهبود بخشید، که این می‌تواند به افزایش نرخ تبدیل بازدیدکنندگان به مشتریان منجر شود. در نهایت، سئو به کاهش هزینه‌های بازاریابی کمک می‌کند، زیرا یک روش پایدار و بلندمدت برای جذب ترافیک است، بدون نیاز به پرداخت مداوم برای تبلیغات. با سرمایه‌گذاری در سئو، سایت شما به یک ابزار قدرتمند برای افزایش فروش و رشد کسب‌وکار تبدیل می‌شود."
          />
          <ServicesImage
            src="assets/images/vectors/seo/seo-1.svg"
            props="!ml-auto"
          />
        </ServicesContainer>
        {/* Ads */}
        <Ads />
        <ServicesContainer>
          <ServicesImage
            src="assets/images/vectors/seo/seo-2.svg"
            props="!ml-auto"
          />
          <ServicesTitleContent
          title="افزایش فروش و بازاریابی"
          content="سئو نه تنها به بهبود رتبه سایت کمک می‌کند، بلکه به طور مستقیم بر فروش و بازاریابی آنلاین شما تاثیر می‌گذارد. با بهینه‌سازی سایت برای کلمات کلیدی مرتبط، مخاطبان هدف به صفحات فروش هدایت شده و نرخ تبدیل و فروش افزایش می‌یابد. این بهبود در رتبه‌بندی و دیده شدن مداوم در نتایج برتر جستجو، اعتماد و شناخت برند را در میان مشتریان تقویت می‌کند. همچنین، سئو امکان تحلیل بازخورد کاربران و بهینه‌سازی محتوا را فراهم می‌کند که تعامل کاربران و وفاداری آنها را افزایش می‌دهد. از سوی دیگر، سئو با کاهش هزینه‌های بازاریابی و جذب ترافیک ارگانیک با کیفیت، به یک روش پایدار برای افزایش فروش و رشد برند تبدیل می‌شود، که در نتیجه می‌توان از رقبا پیشی گرفت و جایگاه بهتری در بازار به دست آورد."
          />
        </ServicesContainer>
        <ServicesContainer>
          <ServicesTitleContent
          title="چرا سئو معین وب"
          content="تیم متخصصان ما در معین وب با استفاده از تکنیک‌های پیشرفته و استراتژی‌های نوین، سایت شما را به بهترین نحو ممکن بهینه‌سازی می‌کنند. ما با تحلیل دقیق بازار و رقبا، استراتژی مناسبی برای سایت شما تدوین می‌کنیم تا شما بتوانید از تمامی مزایای سئو بهره‌مند شوید و در بازار رقابتی به موفقیت برسید. خدمات ما شامل تحلیل جامع کلمات کلیدی، بهبود ساختار سایت، بهینه‌سازی محتوا و ایجاد بک‌لینک‌های با کیفیت است که همه اینها به افزایش رتبه سایت شما در نتایج جستجو کمک می‌کند. با سئو معین وب، سایت شما نه تنها در نتایج جستجو بهبود می‌یابد، بلکه تجربه کاربری نیز بهبود پیدا می‌کند، که این امر به افزایش نرخ تبدیل و در نهایت افزایش فروش منجر می‌شود. ما با رصد مداوم عملکرد سایت و انجام تنظیمات لازم، اطمینان حاصل می‌کنیم که سایت شما همیشه بهینه بماند و در برابر تغییرات الگوریتم‌های موتورهای جستجو مقاوم باشد. با معین وب، شما نه تنها در رتبه‌بندی موتورهای جستجو پیشرفت می‌کنید، بلکه اعتماد و شناخت برند خود را نیز افزایش می‌دهید و در نهایت به جایگاهی پایدار و موفق در بازار دست می‌یابید. این رویکرد جامع و متمرکز بر نتایج، ما را از دیگران متمایز می‌کند و به شما امکان می‌دهد که در دنیای دیجیتال به بهترین نتایج دست یابید."
          />
          <ServicesImage
            src="assets/images/vectors/seo/seo-3.svg"
            props="!ml-auto"
          />
        </ServicesContainer>
        <ServicesStepContainer>
          {
            steps.map
            (item=>
            <ServicesStep
              key={item.id}
              step={item.step}
              title={item.title}
              subtitle={item.subtitle}
            />  
            )
          }
        </ServicesStepContainer>
        <AccordionContainer
          title="سوالات متداول"
          items={faqs}
        >
        </AccordionContainer>
      </div>
    </BaseLayout>
  );
}

export default SeoPage;