//internal
import TopBar from "../Components/TopBar";
import BaseLayout from "../Layout/BaseLayout";
import { ServicesTitleContent, ServicesImage, ServicesContainer, ServicesStepContainer, ServicesStep, AccordionContainer, Ads } from "../Layout/ServicesLayout";

const AdvertisingPage = () => {
  const steps = [
    {id:1, step:"مرحله اول", title:"ثبت درخواست مشاوره", subtitle:"در سایت با اطلاعات واقعی"},
    {id:2, step:"مرحله دوم", title:"مشاوره اولیه ", subtitle:"بررسی نیازها و اهداف تبلیغاتی "},
    {id:3, step:"مرحله سوم", title:"طراحی استراتژی", subtitle:"تدوین استراتژی تبلیغاتی "},
    {id:4, step:"مرحله چهارم", title:"اجرا و مدیریت کمپین", subtitle:"اجرای کمپین و نظارت آن"},
    {id:5, step:"مرحله پنجم", title:"تحلیل و بهینه‌سازی", subtitle:"تایج و بهینه‌سازی کمپین "},
    {id:6, step:"مرحله آخر", title:"گزارش‌دهی", subtitle:"ارائه گزارش‌های جامع "},
  ];
  const faqs = [
    {id:1, title:"تبلیغات دیجیتال چیست؟ ", content:"تبلیغات دیجیتال به استفاده از ابزارها و کانال‌های آنلاین برای تبلیغ محصولات و خدمات اشاره دارد."},
    {id:2, title:"چرا تبلیغات دیجیتال مهم است؟ ", content:"تبلیغات دیجیتال به شما امکان می‌دهد تا به مخاطبان هدف خود به صورت دقیق‌تر و موثرتر دسترسی پیدا کنید."},
    {id:3, title:"هزینه تبلیغات دیجیتال چقدر است؟ ", content:" هزینه‌ها بسته به نوع کمپین و هدف‌های تبلیغاتی متفاوت است."},
    {id:4, title:"چگونه می‌توانم نتایج تبلیغات خود را اندازه‌گیری کنم", content:"با استفاده از ابزارهای تحلیل دیجیتال می‌توانید نتایج و بازدهی کمپین‌های خود را اندازه‌گیری کنید."},
  ];
  return(
    <BaseLayout>
      <TopBar
        title="تبلیغات"
      />
      <div className="child:BaseContainer space-y-28">
        <ServicesTitleContent
        title="تبلیغات  "
        content="تبلیغات دیجیتال به استفاده از کانال‌های آنلاین برای رساندن پیام‌های تبلیغاتی به مخاطبان هدف اشاره دارد. این کانال‌ها شامل موتورهای جستجو، شبکه‌های اجتماعی، ایمیل، وب‌سایت‌ها و برنامه‌های موبایل می‌شود. هدف اصلی تبلیغات دیجیتال، افزایش آگاهی از برند، جذب مشتریان جدید و افزایش فروش است. در دنیای امروز که تکنولوژی و اینترنت نقش بسزایی در زندگی مردم دارند، تبلیغات دیجیتال به یک ابزار اساسی برای کسب‌وکارها تبدیل شده است."
        />
        <ServicesContainer>
          <ServicesTitleContent
          title="انواع تبلیغات دیجیتال"
          content="تبلیغات دیجیتال شامل انواع مختلفی است که هر کدام از آن‌ها به نحوی به افزایش دیده شدن و جذب مخاطب کمک می‌کنند. یکی از اصلی‌ترین انواع تبلیغات دیجیتال، تبلیغات پرداخت به ازای کلیک (PPC) است که معمولاً در موتورهای جستجو مانند گوگل استفاده می‌شود. تبلیغات در شبکه‌های اجتماعی نیز بسیار موثر است، با استفاده از پلتفرم‌هایی مانند فیسبوک، اینستاگرام و توییتر که امکان هدف‌گذاری دقیق مخاطبان را فراهم می‌کنند. ایمیل مارکتینگ به کسب‌وکارها کمک می‌کند تا از طریق ارسال ایمیل‌های هدفمند، مشتریان فعلی و پتانسیل خود را جذب و نگه‌داری کنند. تبلیغات نمایشی شامل بنرها و تبلیغات پاپ‌آپ در وب‌سایت‌ها و برنامه‌های موبایل است که به افزایش ترافیک و نصب اپلیکیشن‌ها کمک می‌کند. تبلیغات ویدیویی، به ویژه در پلتفرم‌هایی مانند یوتیوب، به دلیل جذابیت بصری و توانایی انتقال پیام‌های پیچیده، بسیار موثر هستند. همچنین، تبلیغات محتوایی که به صورت مقالات و وبلاگ‌ها ارائه می‌شود، به بهبود سئو و افزایش آگاهی از برند کمک می‌کند. در نهایت، تبلیغات همکاری در فروش (Affiliate Marketing) و بازاریابی تاثیرگذاران (Influencer Marketing) نیز از روش‌های نوین تبلیغات دیجیتال هستند که به کسب‌وکارها امکان می‌دهند تا از شبکه‌ها و تاثیرگذاری افراد محبوب بهره‌برداری کنند."
          />
          <ServicesImage
            src="assets/images/vectors/application/app-1.svg"
            props="!ml-auto"
          />
        </ServicesContainer>
        {/* Ads */}
        <Ads />
        <ServicesContainer>
          <ServicesImage
            src="assets/images/vectors/application/app-2.svg"
            props="!ml-auto"
          />
          <ServicesTitleContent
          title="چرا تبلیغات کنیم؟"
          content="صرف هزینه برای تبلیغات یک سرمایه‌گذاری حیاتی برای رشد و موفقیت کسب‌وکارها است. تبلیغات باعث افزایش دیده شدن و آگاهی از برند شما می‌شود و در بازاری که رقابت شدید است، کمک می‌کند نام و محصولات شما در ذهن مشتریان بالقوه قرار گیرد. همچنین، تبلیغات می‌تواند مشتریان جدید جذب کرده و فروش را افزایش دهد، زیرا با هدف‌گذاری دقیق، مخاطبان هدف به وب‌سایت یا فروشگاه شما جذب می‌شوند و به مشتریان واقعی تبدیل می‌شوند. علاوه بر این، تبلیغات به شما امکان می‌دهد تا با رقبا رقابت کنید و حتی از آنها پیشی بگیرید. اگر رقبای شما در حال تبلیغ هستند و شما نیستید، بخش بزرگی از بازار را از دست خواهید داد. تبلیغات همچنین به شما کمک می‌کند بازخورد مستقیم از بازار دریافت کرده و استراتژی‌های بازاریابی خود را بهبود دهید تا بهتر به نیازها و خواسته‌های مشتریان پاسخ دهید. در نهایت، تبلیغات به شما امکان می‌دهد که برندسازی کنید و اعتماد مشتریان را جلب کنید، زیرا مشتریان به برندهایی اعتماد می‌کنند که حضور فعالی در بازار دارند. بنابراین، صرف هزینه برای تبلیغات نه تنها هزینه نیست، بلکه سرمایه‌گذاری مهمی برای رشد و پایداری کسب‌وکار شماست."
          />
        </ServicesContainer>
        <ServicesContainer>
          <ServicesTitleContent
          title="خدمات تبلیغات معین وب"
          content="خدمات تبلیغات دیجیتال معین وب مجموعه‌ای کامل از راهکارهای تبلیغاتی است که به کسب‌وکارها کمک می‌کند در فضای آنلاین بیشتر دیده شوند و مخاطبان هدف خود را جذب کنند. این خدمات شامل تبلیغات در پلتفرم یکتانت، تبلیغات گوگل و تبلیغات در پیام‌رسان‌های ایرانی مانند تلگرام و ایتا است. تبلیغات در یکتانت، به عنوان یکی از بزرگترین شبکه‌های تبلیغاتی آنلاین ایران، ترافیک و بازدیدهای هدفمند به وب‌سایت‌ها می‌آورد. در تبلیغات گوگل، معین وب با بهینه‌سازی کلمات کلیدی و تنظیم کمپین‌های دقیق، کسب‌وکارها را در نتایج جستجوی گوگل بالاتر می‌برد و بازدیدکنندگان هدفمندتری جذب می‌کند. همچنین، گزارش‌های دوره‌ای از عملکرد تبلیغات ارائه می‌شود. تبلیغات در پیام‌رسان‌های ایرانی نیز به کسب‌وکارها امکان می‌دهد مستقیماً با کاربران در ارتباط باشند و پیام‌های خود را سریع و موثر به دست مخاطبان برسانند. تیم متخصص معین وب با تحلیل دقیق داده‌ها و استراتژی‌های بهینه‌سازی، بهترین نتایج را از این روش‌ها کسب می‌کند."
          />
          <ServicesImage
            src="assets/images/vectors/application/app-3.svg"
            props="!ml-auto"
          />
        </ServicesContainer>
        <ServicesStepContainer>
          {
            steps.map
            (item=>
            <ServicesStep
              key={item.id}
              step={item.step}
              title={item.title}
              subtitle={item.subtitle}
            />  
            )
          }
        </ServicesStepContainer>
        <AccordionContainer
          title="سوالات متداول"
          items={faqs}
        >
        </AccordionContainer>
      </div>
    </BaseLayout>
  );
}

export default AdvertisingPage;